export class getLists {
    id: number;
    title: string;
    zan: number;
    info: number;
    watch: number;
    name: string;
    time: string;
    action: string;
    constructor(
        id: number,
        title: string,
        zan: number,
        info: number,
        watch: number,
        name: string,
        time: string,
        action: string,
    ) {
        this.id = id;
        this.title = title;
        this.zan = zan;
        this.info = info;
        this.watch = watch;
        this.name = name;
        this.time = time;
        this.action = action
    }
    
}
